// Color
$success: #8BC34A;
$info: #03A9F4;
$warning: #FFC107;
$error: #F44336;
// Font
$title-size: 16px;
$content-size: 13px;

.angular-notify{
	color: #fff;
	font-family: 'Roboto', system, -apple-system, '.SFNSDisplay-Regular', HelveticaNeue, Arial, 'Noto Sans', 'Noto Sans CJK SC', 'PingFang SC', 'Hiragino Sans GB', 'Microsoft Yahei', sans-serif;

	border-radius: 2px;
	box-shadow: 0 4px 4px rgba(0, 0, 0, 0.24), 0 0 4px rgba(0, 0, 0, 0.12);
	width: 480px;
	padding: 15px 40px 13px 0;
	margin: 16px;
	position: relative;
	z-index: 999;

	.title{
		font-size: $title-size;
		font-weight: 600;
		letter-spacing: 0.02em;
		margin: 0 0 0 64px;
		padding: 0 0 0 16px;
	}

	.content{
		font-size: $content-size;
		letter-spacing: 0.02em;
		margin: 10px 0 0 64px;
		padding: 0 0 0 16px;
	}

	.close{
		background: none;
		border: none;
		outline: none;
		width: 24px;
		height: 24px;
		position: absolute;
		top: 13px;
		right: 16px;
		i{
			color: #fff;
		}
		&:hover{
			opacity: 0.6;
		}
	}

	&:before{
		background-color: rgba(0, 0, 0, 0.14);
		display: flex;
		justify-content: center;
		align-items: center;
		width: 64px;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
	}
}

